.main {
    margin-top: 3%;
}

.posts-card:hover {
    cursor: pointer;
    background-color: #0d6dfd50;
}

.pagesDiv {
    justify-content: center;
    text-align: center;
}

.page-nos {
    cursor: pointer;
}

.active-page {
    background-color: rgba(128, 128, 128, 0.192);
}

.loader {
    text-align: center;
}

#show-comments:hover {
    cursor: pointer;
    text-decoration: underline;
}

#delete-post:hover{
    cursor: pointer;
    text-decoration: underline; 
}

.posts-card {
    list-style: none;
    margin: 6px 6px 10px 6px;
    padding: 10px 10px 10px 10px;
    background-color: rgba(149, 149, 149, 0.164);
}

@media (min-width:320px) {
    /* smartphones, iPhone, portrait 480x320 phones */
    .filter-form {
        margin-top: 5px;
        max-width: 100%;
    }
    .userPhoto {
        font-size: 2em;
    }
    .userCard {
        max-width: 100%;
        margin-top: 5px;
    }
    .postsOfuserCard {
        margin-top: 15px;
    }
    .user-post-nav {
        display: block;
    }
    .posts {
        min-height: 300px;
        max-height: 400px;
        overflow-y: auto;
    }
    .pagesDiv {
        margin-bottom: 0px;
        margin-top: 20px;
    }
    .comment{
        list-style: none;
        margin:10px 6px 10px 6px;
    }
    #delete-post{
     margin-left: auto;
    }
    
}

@media (min-width:481px) {
    /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */
}

@media (min-width:641px) {
    /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
}

@media (min-width:961px) {
    .user-post-nav {
        display: flex;
    }
    /* tablet, landscape iPad, lo-res laptops ands desktops */
}

@media (min-width:1025px) {
    .filter-form {
        margin-top: 0px;
        max-width: 40%;
    }
    .userPhoto {
        font-size: 3em;
    }
    .userCard {
        max-width: 50%;
        margin-top: 5px;
    }
    /* big landscape tablets, laptops, and desktops */
    .pagesDiv {
        margin-bottom: 0px;
        margin-top: 0px;
    }
    .comment{
        max-width: 60%;
        list-style: none;
    }
    #delete-post{
        margin-left: 30px;
       }
}

@media (min-width:1281px) {
    /* hi-res laptops and desktops */
}

.donutSpinner {
    display: inline-block;
    border: 4px solid #0a58ca;
    border-left-color: #ffffff;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    margin-top: 7%;
    animation: donut-spin 1.2s linear infinite;
}

@keyframes donut-spin {
    to {
        transform: rotate(1turn);
    }
}